"use client"

import { useState, useEffect } from 'react'
import QRCodeGenerator from './QRCodeGenerator'

interface InventoryItem {
  id: string
  manufacturer: string
  manufacturing_date: string
  condition: string
  transaction_date: string
  buy_date?: string
  repair_date?: string
  install_date?: string
  description?: string
}

export default function InventoryTable() {
  const [inventoryData, setInventoryData] = useState<InventoryItem[]>([])
  const [selectedPartId, setSelectedPartId] = useState<string | null>(null)
  const [searchTerm, setSearchTerm] = useState('')
  const [filterCondition, setFilterCondition] = useState('all')

  useEffect(() => {
    loadInventoryData()
  }, [])

  const loadInventoryData = () => {
    try {
      const data = localStorage.getItem('inventoryData')
      if (data) {
        setInventoryData(JSON.parse(data))
      }
    } catch (error) {
      console.error('Failed to load inventory data:', error)
    }
  }

  const filteredData = inventoryData.filter(item => {
    const matchesSearch = item.id.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         item.manufacturer.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         (item.description?.toLowerCase().includes(searchTerm.toLowerCase()) ?? false)
    
    const matchesCondition = filterCondition === 'all' || item.condition === filterCondition

    return matchesSearch && matchesCondition
  })

  const getConditionBadge = (condition: string) => {
    const baseClasses = "px-2 py-1 rounded-full text-xs font-medium"
    switch (condition.toLowerCase()) {
      case 'brand new':
        return `${baseClasses} bg-green-100 text-green-800`
      case 'used':
        return `${baseClasses} bg-yellow-100 text-yellow-800`
      case 'repaired':
        return `${baseClasses} bg-blue-100 text-blue-800`
      default:
        return `${baseClasses} bg-gray-100 text-gray-800`
    }
  }

  if (inventoryData.length === 0) {
    return (
      <div className="bg-white rounded-lg shadow-md p-8 text-center">
        <div className="text-gray-400 mb-4">
          <div className="w-16 h-16 mx-auto mb-4 bg-gray-100 rounded-full flex items-center justify-center">
            <span className="text-2xl">üì¶</span>
          </div>
        </div>
        <h3 className="text-lg font-medium text-gray-900 mb-2">No Inventory Data</h3>
        <p className="text-gray-600 mb-4">
          Upload a CSV file to view your inventory and generate QR codes.
        </p>
        <a 
          href="/upload" 
          className="inline-block bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition-colors"
        >
          Upload CSV File
        </a>
      </div>
    )
  }

  return (
    <div className="bg-white rounded-lg shadow-md">
      <div className="p-6 border-b border-gray-200">
        <h2 className="text-xl font-semibold text-gray-900 mb-4">Inventory Items</h2>
        
        <div className="flex flex-col sm:flex-row gap-4 mb-4">
          <div className="flex-1">
            <input
              type="text"
              placeholder="Search by ID, manufacturer, or description..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>
          <div>
            <select
              value={filterCondition}
              onChange={(e) => setFilterCondition(e.target.value)}
              className="px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="all">All Conditions</option>
              <option value="brand new">Brand New</option>
              <option value="used">Used</option>
              <option value="repaired">Repaired</option>
            </select>
          </div>
        </div>

        <p className="text-sm text-gray-600">
          Showing {filteredData.length} of {inventoryData.length} items
        </p>
      </div>

      <div className="overflow-x-auto">
        <table className="w-full">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Part ID
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Manufacturer
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Condition
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Description
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {filteredData.map((item) => (
              <tr key={item.id} className="hover:bg-gray-50">
                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                  {item.id}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                  {item.manufacturer}
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className={getConditionBadge(item.condition)}>
                    {item.condition}
                  </span>
                </td>
                <td className="px-6 py-4 text-sm text-gray-900">
                  <div className="max-w-xs truncate">
                    {item.description || 'No description'}
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm">
                  <button
                    onClick={() => setSelectedPartId(item.id)}
                    className="bg-blue-600 text-white px-3 py-1 rounded-md hover:bg-blue-700 transition-colors text-xs"
                  >
                    Generate QR
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {selectedPartId && (
        <QRCodeGenerator
          partId={selectedPartId}
          onClose={() => setSelectedPartId(null)}
        />
      )}
    </div>
  )
}
