"use client"

import { useState, useEffect } from 'react'
import QRCode from 'qrcode'

interface QRCodeGeneratorProps {
  partId: string
  onClose?: () => void
}

export default function QRCodeGenerator({ partId, onClose }: QRCodeGeneratorProps) {
  const [qrCodeUrl, setQrCodeUrl] = useState<string>('')
  const [isGenerating, setIsGenerating] = useState(false)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    generateQRCode()
  }, [partId])

  const generateQRCode = async () => {
    if (!partId) return

    setIsGenerating(true)
    setError(null)

    try {
      // Create QR code data with part ID
      const qrData = JSON.stringify({
        type: 'bus_part',
        id: partId,
        timestamp: new Date().toISOString()
      })

      const url = await QRCode.toDataURL(qrData, {
        width: 256,
        margin: 2,
        color: {
          dark: '#000000',
          light: '#FFFFFF'
        }
      })

      setQrCodeUrl(url)
    } catch (err) {
      setError('Failed to generate QR code')
      console.error('QR Code generation error:', err)
    } finally {
      setIsGenerating(false)
    }
  }

  const downloadQRCode = () => {
    if (!qrCodeUrl) return

    const link = document.createElement('a')
    link.href = qrCodeUrl
    link.download = `qr-code-${partId}.png`
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-sm w-full p-6">
        <div className="flex justify-between items-center mb-4">
          <h3 className="text-lg font-semibold text-gray-900">QR Code for {partId}</h3>
          {onClose && (
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600 text-xl"
            >
              √ó
            </button>
          )}
        </div>

        {isGenerating && (
          <div className="flex items-center justify-center py-8">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
            <span className="ml-2 text-gray-600">Generating QR code...</span>
          </div>
        )}

        {error && (
          <div className="bg-red-50 border border-red-200 rounded-md p-3 mb-4">
            <p className="text-red-800 text-sm">{error}</p>
          </div>
        )}

        {qrCodeUrl && !isGenerating && (
          <div className="text-center">
            <div className="bg-gray-50 p-4 rounded-lg mb-4">
              <img 
                src={qrCodeUrl} 
                alt={`QR Code for ${partId}`}
                className="mx-auto"
                style={{ imageRendering: 'pixelated' }}
              />
            </div>
            
            <div className="space-y-3">
              <button
                onClick={downloadQRCode}
                className="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 transition-colors"
              >
                Download QR Code
              </button>
              
              <button
                onClick={generateQRCode}
                className="w-full bg-gray-200 text-gray-800 py-2 px-4 rounded-md hover:bg-gray-300 transition-colors"
              >
                Regenerate
              </button>
            </div>
          </div>
        )}

        <div className="mt-4 text-xs text-gray-500 text-center">
          <p>Scan this QR code with the mobile app to view part details</p>
        </div>
      </div>
    </div>
  )
}
